import React, { Component } from 'react';
import Loadable from "react-loadable";
import Loading from './Components/Loading';
import { Header } from "./Components";
import { About, Contact, FileStructure, SetStructure, TopicStructure, TypeStructure, Repository } from "./pages";
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import './Lato.css';
import "./App.css";

const FacebookChat = Loadable({
    loader: () => import("./Components/fbmChat"),
    loading: Loading
});

class App extends Component {
  render() {
    return (
      <Router>
        <div className="view">
          <Header />
          <Route exact path="/" component={SetStructure} />
          <Switch>
            <Route exact path="/list" component={Repository} />
            <Route exact path="/about" component={About} />
            <Route exact path="/contact" component={Contact} />
            <Route path="/:set/:type/:topic" component={FileStructure} />
            <Route path="/:set/:type" component={TopicStructure} />
            <Route path="/:set" component={TypeStructure} />
          </Switch>
          <FacebookChat />
        </div>
      </Router>
    );
  }
}

export default App;
